<template>
  <el-container >
    <el-aside style="width:40%;background:#fff">
      <el-row style="background-color: #EAF1F5">
        <el-col :span="18">
          <el-tag size="small"></el-tag>
        </el-col>
        <el-col :span="5" style="text-align: center">
          <el-button type="text" size="mini" class="el-icon-circle-plus"  @click="dialogTableVisible = true"></el-button>
        </el-col>
        <el-dialog title="" :visible.sync="dialogTableVisible" width="30%">
          <el-form ref="form01" :model="dialogForm01" label-width="80px" size="mini">
            <el-form-item label="">
              <el-select  placeholder="" v-model="dialogForm01.Scope">
                <el-option label="" value="3"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input placeholder="" v-model="dialogForm01.Name"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="dialogTableVisible=false" type="warning"></el-button>
              <el-button type="primary" @click="addNewData('form01')"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </el-row>
      <el-row style="margin-top: 10px;">
        <el-form :inline="true" :model="searchForm01" class="demo-form-inline" size="mini">
          <el-form-item label="">
            <el-input v-model="searchForm01.Name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="  ">
            <el-select v-model="searchForm01.Scope" placeholder="" style="width: 100px">
              <el-option label="" value=""></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="searchTemplate"></el-button>
          </el-form-item>
        </el-form>
      </el-row>

      <el-row style="margin-top: 10px">
        <el-table ref="singleTable" :data="templateData01" highlight-current-row @current-change="handleCurrentChange01"
                  style="width: 100%" size="mini">
          <el-table-column property="Name" label="" >
          </el-table-column>
          <el-table-column property="CreationTime" label="" width="130">
            <template slot-scope="scope">
              {{ scope.row.CreationTime|formatDateTime }}
            </template>
          </el-table-column>
          <el-table-column property="Scope" label="" width="100">
            <template slot-scope="scope">
              {{ scope.row.Scope|showScope }}
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-aside>
    <el-main style="width:60%;height: 1200px;">
        <el-row style="background-color: #EAF1F5">
          <el-col :span="4">
            <el-tag type="info" ></el-tag>
          </el-col>
          <el-col :span="5" style="text-align: right">
            <el-button type="text" size="small" class="el-icon-circle-plus-outline" @click="showDrugsList"></el-button>
          </el-col>
          <el-dialog title="" :visible.sync="dialogDrugsVisible" top="10px" width="80%">
            <el-row>
              <el-col :span="18">
                <div class="grid-content bg-purple">
                  <el-input placeholder="" size="mini" v-model="keywords01">
                  </el-input>
                </div>
              </el-col>
              <el-col :span="6">
                <div class="grid-content bg-purple">
                  <el-button type="primary" size="mini" style="margin-left: 10px" icon="el-icon-view" @click="searchData"></el-button>
                </div>
              </el-col>
            </el-row>
            <el-table stripe ref="multipleTable" size="mini"
              :data="categories" tooltip-effect="dark" style="width: 100%">
              <el-table-column label="" prop="DrugsCode" width="140" align="left"></el-table-column>
              <el-table-column label="" prop="DrugsName" align="left"></el-table-column>
              <el-table-column label="" prop="DrugsFormat" width="140" align="left"></el-table-column>
              <el-table-column label="" prop="DrugsUnit" width="80" align="left"></el-table-column>
              <el-table-column label="" prop="DrugsPrice" width="80" align="left"></el-table-column>
              <el-table-column label="" prop="DrugsDosageID" :formatter="showFormatConstants"  width="80" align="left"></el-table-column>
              <el-table-column label="" prop="DrugsTypeID" :formatter="showFormatConstants" width="80" align="left"></el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <el-button type="primary"
                             size="mini"
                             @click="showInnerDialog(scope.$index, scope.row)">
                  </el-button>
                </template>
                <el-dialog width="30%" title="" :visible.sync="innerVisible" append-to-body center="center">
                  <el-form :label-position="labelPosition" label-width="100px"  :model="formEdit02" class="demo-form-inline" size="mini">
                    <el-form-item label="">
                      <el-input v-model="formEdit02.DrugsCode" placeholder="" readonly="readonly" ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="formEdit02.DrugsName" placeholder="" readonly="readonly" ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="formEdit02.DrugsFormat" placeholder="" readonly="readonly" ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="formEdit02.DrugsUnit" placeholder="" readonly="readonly"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="formEdit02.Usage" placeholder="" ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="formEdit02.Dosage" placeholder="" ></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="formEdit02.Frequency" placeholder="" ></el-input>
                    </el-form-item>
                    <el-form-item>
                      <el-button type="danger" style="width: 100px;" @click="addDrugsDetailed"  >
                      </el-button>
                    </el-form-item>
                  </el-form>
                </el-dialog>
              </el-table-column>
            </el-table>
            <el-row>
              <el-col :span="8" align="left">
                <el-button type="danger" style="margin-top: 10px;width: 100px;" size="mini"
                           @click="closeDrugsDialog"  >
                </el-button>
              </el-col>
              <el-col :span="16" align="right">
                <el-pagination
                  background
                  :page-size="pageSize" :current-page.sync="currentPage01"
                  layout="prev, pager, next"
                  :total="totalCount" @current-change="currentChange" v-show="this.categories.length>0">
                </el-pagination>
              </el-col>
            </el-row>
          </el-dialog>
          <el-col :span="5" style="text-align: right">
            <el-button type="text" size="small" class="el-icon-close" @click="deleteTmplDrugs"  :disabled="this.selItems.length==0"></el-button>
          </el-col>
          <el-col :span="5" style="text-align: right">
            <el-button type="text" size="small" class="el-icon-check" @click="updataTemplate"></el-button>
          </el-col>
          <el-col :span="5" style="text-align: right">
            <el-button type="text" size="small" class="el-icon-delete" @click="deleteTemplate"></el-button>
          </el-col>
        </el-row>
      <el-row style="margin-top: 10px">
        <el-col>
          <el-tag size="mini" style="width: 100%;text-align: center"></el-tag>
        </el-col>
      </el-row>

        <el-form inline="inline" :model="formEdit03" size="small">
          <el-tag  type="info"></el-tag>
          <el-form-item style="width: 50%">
            <el-input  v-model="formEdit03.Name" style="width: 200px"></el-input>
          </el-form-item>
            <el-form-item label="">
            <el-select v-model="formEdit03.Scope"  placeholder="" style="width: 100px" >
              <el-option label="" value="3"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
            </el-form-item>
        </el-form>
      <el-row >
          <el-table ref="multipleTable02" :data="templateDrugs" style="width: 100%" @selection-change="handleSelectionChange01" size="mini" >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column property="DrugsName" label="" >
            </el-table-column>
            <el-table-column property="DrugsFormat" label="" >
            </el-table-column>
            <el-table-column property="DrugsUnit" label="" width="50">
            </el-table-column>
            <el-table-column property="DrugsUsage" label="" width="50">
            </el-table-column>
            <el-table-column property="DrugsDosage" label="" width="50">
            </el-table-column>
            <el-table-column property="DrugsFrequency" label="" width="100">
            </el-table-column>
          </el-table>
      </el-row>
    </el-main>

  </el-container>
</template>

<script>
  import {showMsgTitle} from '../../../utils/api'
  import  {showMsgConfirm} from '../../../utils/api'
  import  {showConstants} from '../../../utils/api'
  import {postRequest}   from '../../../utils/api'
  import {putRequest}    from '../../../utils/api'
  import {deleteRequest} from '../../../utils/api'
  import {getRequest}    from '../../../utils/api'
  export default {
    mounted:function(){
      this.loadDatas('','');
      //this.loadDrugs(1, this.pageSize,this.keywords01);
    },
    methods:{
      onSubmit:function(){
        this.dialogTableVisible=false;
      },
      showFormatConstants(row, column, cellValue, index){
        return showConstants(row, column, cellValue, index);
      },
      handleSelectionChange01:function(val){
        // alert(val[0].ID);
        // alert(JSON.stringify(val));
        this.selItems = val;
      },
      handleCurrentChange01:function(val){
        this.formEdit03.ID=val.ID;
        this.formEdit03.Name=val.Name;
        this.formEdit03.Scope = val.Scope +'';
        this.showTmplDrugs(val.ID);
      },
      //
      updataTemplate(){
        let _this = this;
        this.$confirm(' ,?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          putRequest('/neupha/template/', this.formEdit03).then(resp => {
            if (resp.status == 200) {
              let json = resp.data;
              _this.$message({type: json.status, message: json.msg});
              // _this.formEdit03 = {};
              _this.loadDatas('', '');
            }
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      deleteTemplate(){
        let _this = this;
        this.$confirm(' ,?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteRequest('/neupha/template/drug/'+this.formEdit03.ID).then(resp => {
            if (resp.status == 200) {
              let json = resp.data;
              _this.$message({type: json.status, message: json.msg});
              _this.formEdit03 = {};
              _this.loadDatas('', '');
              _this.showTmplDrugs(this.formEdit03.ID);
            }
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      closeDrugsDialog(){
        this.dialogDrugsVisible=false;
        this.showTmplDrugs(this.formEdit03.ID);
      },
      //
      showTmplDrugs(id){
        let _this = this;
        let url = "/neupha/templateDetaile/listTemplateDrugs?keywords01="+id;
        getRequest(url).then(resp=>{
          if(resp.status == 200){
            _this.templateDrugs = resp.data ;
            // alert(_this.templateDrugs[0].ID);
          }else{
            _this.templateData01.Name = '';
          }
        }).catch(resp=>{
          _this.$message({type:'error',message:''});
        });
      },
      searchTemplate(){
        this.loadDatas(this.searchForm01.Name,this.searchForm01.Scope);
      },
      showDrugsList(){
        if(this.formEdit03.ID==null || this.formEdit03.ID==''){
          this.$message({type: 'error', message: '!'});
        }else {
          this.loadDrugs(1, this.pageSize,this.keywords01);
          this.dialogDrugsVisible=true;
        }
      },
      addNewData(formName){
        let _this = this;
        postRequest('/neupha/template/', this.dialogForm01).then(resp => {
          if (resp.status == 200) {
            let json = resp.data;
            _this.$message({type: json.status, message: json.msg});
            _this.dialogForm01 = {};
            _this.loadDatas('', '');
          }
        });
        _this.dialogTableVisible = false;
      },
      // -- 
      loadDatas(keywords01,keywords02){
        let _this = this;
        let url = "/neupha/template/listDrugTemplate?keywords01="+keywords01+"&keywords02="+keywords02;
        getRequest(url).then(resp=>{
            if(resp.status == 200){
                _this.templateData01 = resp.data ;
            }else{
              _this.templateData01.Name = '';
            }
          }).catch(resp=>{
            _this.$message({type:'error',message:''});
        });
      },

      // -- 
      loadDrugs(pageNumber, pageSize ,keywords01){
        let _this = this;
        let url = "/neupha/drug/listPage?pageNumber=" + pageNumber + "&pageSize=" + pageSize + "&keywords01=" + keywords01 ;
        getRequest(url).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            _this.categories = resp.data.list;
            _this.totalCount = resp.data.totalCount;
            _this.currentPage01 = pageNumber ;
          } else {
            _this.$message({type: 'error', message: '!'});
          }
        }, resp=> {
          _this.loading = false;
          if (resp.response.status == 403) {
            _this.$message({type: 'error', message: resp.response.data});
          } else {
            _this.$message({type: 'error', message: '!'});
          }
        }).catch(resp=> {
          //
          _this.loading = false;
          _this.$message({type: 'error', message: '!'});
        })
      },
      //
      currentChange(currentPage){
        this.currentPage01 = currentPage;
        this.loading = true;
        this.loadDrugs(currentPage, this.pageSize,this.keywords01);
      },
      //
      searchData(){
        if(this.keywords01==null || this.keywords01==''){
          this.loadDrugs(1, this.pageSize,'');
        }else{
          this.loadDrugs(1, this.pageSize,this.keywords01);
        }
      },

      //
      showInnerDialog(index,row){
          this.innerVisible=true;
          this.formEdit02 = row;
          this.formEdit02.DrugsDosage = row.Dosage;
          this.formEdit02.DrugtempId = this.formEdit03.ID;
          this.formEdit02.DrugsId = row.ID;
          this.formEdit02.Usage='';
          this.formEdit02.Dosage='';
          this.formEdit02.Frequency='';
      },
      //
      addDrugsDetailed(){
        let _this = this;
        postRequest('/neupha/templateDetaile/drugs',this.formEdit02).then(resp=> {
          if(resp.status == 200){
            let json = resp.data;
            _this.$message({type:json.status,message:json.msg});
            _this.formEdit02 = {};
            this.innerVisible=false;
          }else{
            _this.$message({type:'warm',message:''});
          }
        });
      },
      //
      deleteTmplDrugs(){
        let _this = this;
        this.$confirm(' ' +  this.selItems.length + ' ?', '', {
          type: 'warning',
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(()=> {
          let selItems = _this.selItems;
          let ids = '';
          for (let i = 0; i < selItems.length; i++) {
            ids += selItems[i].ID + ",";
          }
          // alert(ids);
          this.deleteTmplDrug(ids);

          this.$message({type:'success',message:""+ this.selItems.length + ' '});
        }).catch(() => {
          //
          //_this.loading = false;
        });
      },
      deleteTmplDrug(ids){
        let _this = this;
        //this.loading = true;
        //
        deleteRequest('/neupha/templateDetaile/drugs/' + ids).then(resp=> {
          let json = resp.data;
          _this.$message({
            type: json.status,
            message: json.msg
          });
          this.showTmplDrugs(this.formEdit03.ID);
        }, resp=> {
          _this.loading = false;
          if (resp.response.status == 403) {
            _this.$message({
              type: 'error',
              message: resp.response.data
            });
          } else if (resp.response.status == 500) {
            _this.$message({
              type: 'error',
              message: '!'
            });
          }
        })
      },
    },
    data() {
      return {
        dialogTableVisible: false,
        dialogDrugsVisible:false,
        innerVisible:false,
        selItems:[],
        labelPosition:'right',
        dialogForm01:{
          Name:'',Scope:'1',UserID:'1',CreationTime:'2019-03-08 10:18'
        },
        searchForm01:{
          Name:'',Scope:''
        },
        templateData01: [],
        currentPage01:1,
        pageSize:10,
        totalCount:0,
        keywords01: '',
        categories:[{ MnemonicCode:'ZSYJAZZ',DrugsCode:'242413242', DrugsName:'', Format:'1g1', Unit:'', Price:'15.73', Dosage:'', DrugsType:'',}],
        formEdit02:{ID:'',DrugtempId:'',DrugsId:'',DrugsName:'',Format:'',Unit:'',Usage:'',Dosage:'',Frequency:'',  DrugsDosage:'',DrugsCode:''},
        templateDrugs:[{ID:'',DrugstempId:'',DrugsId:'',DrugsName:'',DrugsFormat:'',DrugsUnit:'',DrugsUsage:'',DrugsDosage:'',DrugsFrequency:''}],
        formEdit03:{ID:'',Name:'',Scope:''},


        siteName:'',
        radio01:1,

        eltab01:'first',
        eltab02:'',




        tableData03: [],
      }
    },
    filters:{
      showScope:function(scope){
        if(scope==1){
          return '';
        }else if(scope==2){
          return '';
        }else{
          return '';
        }
      }
    },
  }
</script>

<style>
</style>



