            </el-select>
          </el-form-item>
          <el-form-item label="" >
            <el-switch
              v-model="switch01"
              active-color="#13ce66">
            </el-switch>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="dialog01Visible = false"> </el-button>
            <el-button @click="dialog01Visible = false"> </el-button>
          </el-form-item>
        </el-form>
      </el-dialog>


      <!--  -->
      <el-aside  style="width: 700px;height: 500px;">

        <el-row style="background-color: #EAF1F5">
          <el-col :span="8" style="margin-top: 4px;">
            <el-tag size="mini"></el-tag>
            <el-tag type="warning" size="mini">235.00</el-tag>
          </el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-circle-plus" @click="dialog01Visible=true"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-remove" @click="showConfirm('')"></el-button></el-col>
          <el-col :span="2"><el-button type="text" size="mini" class="el-icon-tickets" @click="showMsg('')"></el-button></el-col>
          <el-col :span="2"><el-button type="text" size="mini" class="el-icon-success" @click="showConfirm('')"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-delete" @click="showConfirm('')"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-circle-plus-outline" @click="showConfirm('')"></el-button></el-col>
        </el-row>

        <el-form ref="form"  label-width="80px">
          <el-row>
            <el-col :span="2" style="margin-top: 40px;">
              <el-input type="textarea" :rows="4" :disabled="true" style="width: 45px;" v-model="msg01">

              </el-input>
            </el-col>
            <el-col :span="22" style="margin-top: 4px;">
              <el-table ref="singleTable" :data="tableData02"
                        style="width: 100%;margin: 0px;padding: 0px;" :show-header="true">
                <el-table-column  type="selection">
                </el-table-column>
                <el-table-column property="feilei" label="" width="80">
                </el-table-column>
                <el-table-column property="xiangmu" label="" >
                </el-table-column>
                <el-table-column property="keshi" label="" width="80">
                </el-table-column>
                <el-table-column property="jiaji" label="" width="50">
                </el-table-column>
                <el-table-column property="zhuangtai" label="" width="80">
                </el-table-column>
                <el-table-column property="danjia" label="" width="80">
                </el-table-column>
                <el-table-column property="jieguo" label="" width="80">
                  <template slot-scope="scope">
                    <el-popover
                      placement="right"
                      width="400"
                      trigger="click">
                      <img src="static/imgs/img01.jpg"></img>
                      <el-button slot="reference" type="text" size="mini"></el-button>
                    </el-popover>

                  </template>
                </el-table-column>

              </el-table>
            </el-col>
          </el-row>

          <el-row>
            <el-form-item label="">
              <el-input type="textarea" :rows="4" placeholder=""></el-input>
            </el-form-item>
          </el-row>

        </el-form>
      </el-aside>
      <el-main style="margin-top: -20px;">
        <el-tabs type="card" v-model="activeName">
          <el-tab-pane label="" name="second" >

            <el-table :data="tableData04" style="width: 100%" ref="singleTable" >
              <el-table-column prop="name" label="">
              </el-table-column>
              <el-table-column fixed="right" label="" width="120">
                <template slot-scope="scope">
                  <el-button @click.native.prevent="showConfirm('')"
                             type="text" size="small">
                    
                  </el-button>
                  <el-button @click.native.prevent="showConfirm('')"
                             type="text" size="small">
                    
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>

        </el-tabs>
      </el-main>
    </el-container>

  </div>
</template>

<script>
  import {showMsgTitle} from '../../../utils/api'
  import  {showMsgConfirm} from '../../../utils/api'
  export default {
    methods: {
      showConfirm(msg) {
        showMsgConfirm(this, msg);
      },
      showMsg(msg) {
        showMsgTitle(this, msg);
      }
    },
    data() {
      return {
        dialog01Visible:false,
        switch01:false,

        msg01:'',
        tableData02:[{
          feilei:'',
          xiangmu:'',
          keshi:'',
          jiaji:'',
          zhuangtai:'',
          danjia:'20.00',
        },{
          feilei:'',
          xiangmu:'',
          keshi:'',
          jiaji:'',
          zhuangtai:'',
          danjia:'36.00',
        },{
          feilei:'',
          xiangmu:'',
          keshi:'',
          jiaji:'',
          zhuangtai:'',
          danjia:'10.00',
        }],
        tableData03:[{
          name:''
        },{
          name:''
        },{
          name:''
        }],
        tableData04:[{
          name:''
        },{
          name:''
        },{
          name:''
        }],
        selectData01:[{
          keshi:'',
        },{
          keshi:'',
        },{
          keshi:'',
        },{
          keshi:'',
        }],

        activeName: 'second',

      }
    }
  }
</script>

<style scoped>
</style>



