<template>
  <!--  -->
  <el-container >
    <el-aside style="width:40%;background:#fff">
      <el-row style="background-color: #EAF1F5">
        <el-col :span="18">
          <el-tag size="small"></el-tag>
        </el-col>
        <el-col :span="5" style="text-align: center">
          <el-button type="text" size="mini" class="el-icon-circle-plus"  @click="dialogTableVisible = true"></el-button>
        </el-col>
        <el-dialog title="" :visible.sync="dialogTableVisible" width="30%">
          <el-form ref="form01" :model="dialogForm01" label-width="80px" size="mini">
            <el-form-item label="">
              <el-select  placeholder="" v-model="dialogForm01.Scope">
                <el-option label="" value="3"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-input placeholder="" v-model="dialogForm01.Name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select  placeholder="" v-model="dialogForm01.RecordType">
                <el-option label="" value="3"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="1"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button @click="dialogTableVisible=false" type="warning"></el-button>
              <el-button type="primary" @click="addNewData"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </el-row>

       <el-form :inline="true" :model="searchForm01" class="demo-form-inline" size="mini">
       <el-row style="margin-top: 10px;">
          <el-form-item label="">
            <el-input v-model="searchForm01.Name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="  ">
            <el-select v-model="searchForm01.Scope" placeholder="" style="width: 100px">
              <el-option label="" value=""></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
      </el-row>
      <el-row>
        <el-form-item label="">
          <el-select v-model="searchForm01.RecordType" placeholder="" style="width: 100px">
            <el-option label="" value=""></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item >
          <el-button type="primary" @click="searchTemplate"></el-button>
        </el-form-item>
      </el-row>
      </el-form>

      <el-row style="margin-top: 10px">
        <el-table ref="singleTable" :data="templateData01" highlight-current-row @current-change="handleCurrentChange01"
                  style="width: 100%" size="mini">
          <el-table-column property="Name" label="" >
          </el-table-column>
          <el-table-column property="CreationTime" label="" width="130">
            <template slot-scope="scope">
              {{ scope.row.CreationTime|formatDateTime }}
            </template>
          </el-table-column>
          <el-table-column property="Scope" label="" width="100">
            <template slot-scope="scope">
              {{ scope.row.Scope|showScope }}
            </template>
          </el-table-column>
          <el-table-column property="RecordType" label="" width="100">
            <template slot-scope="scope">
              {{ scope.row.RecordType|showType }}
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-aside>
    <el-main style="width:60%;height: 1200px;">
      <el-row style="background-color: #EAF1F5">
        <el-col :span="14">
          <el-tag type="info" ></el-tag>
        </el-col>
        <el-col :span="5" style="text-align: right">
          <el-button type="text" size="small" class="el-icon-check" @click="updataTemplate"></el-button>
        </el-col>
        <el-col :span="5" style="text-align: right">
          <el-button type="text" size="small" class="el-icon-delete" @click="deleteTemplate"></el-button>
        </el-col>
      </el-row>
      <el-row style="margin-top: 10px">
        <el-col>
          <el-tag size="mini" style="width: 100%;text-align: center"></el-tag>
        </el-col>
      </el-row>
      <el-form :model="formEdit03" label-width="200px" style="margin-top: 10px">
        <el-form-item label="">
          <el-input  v-model="formEdit03.Name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-tag type="info">{{ formEdit03.CreationTime | formatDateTime}}</el-tag>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="formEdit03.Scope"  placeholder="">
            <el-option label="" value="3"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="1"></el-option>
          </el-select>
        </el-form-item>
          <el-form-item label="">
            <el-select v-model="formEdit03.RecordType"  placeholder=""  >
              <el-option label="" value="3"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
        </el-form-item>
      </el-form>
      <el-row>
        <el-button @click="showFme" type="primary"></el-button>
      </el-row>
      <!-- ---start -->
      <el-dialog title="" :visible.sync="dialogFormVisible" width="80%">
        <el-row>
          <el-col :span="18">
            <div class="grid-content bg-purple">
              <el-input placeholder="" size="mini" v-model="keywords01">
              </el-input>
            </div>
          </el-col>
          <el-col :span="6">
            <div class="grid-content bg-purple">
              <el-button type="primary" size="mini" style="margin-left: 10px" icon="el-icon-view" @click="searchData"></el-button>
            </div>
          </el-col>
        </el-row>
        <el-table stripe ref="multipleTable" size="mini" :data="categories" tooltip-effect="dark" style="width: 100%">
          <el-table-column label="" prop="ItemCode" width="140" align="left" ></el-table-column>
          <el-table-column label="" prop="ItemName" align="left"></el-table-column>
          <el-table-column label="" prop="Format" width="80" align="left"></el-table-column>
          <el-table-column label="" prop="Price" width="80" align="left"></el-table-column>
          <el-table-column label="" prop="MnemonicCode" width="180" align="left"></el-table-column>
          <el-table-column label="" align="center" width="80">
            <template slot-scope="scope">
              <el-button type="primary"  size="mini" @click="showInnerDialog(scope.$index, scope.row)">
              </el-button>
            </template>
            <el-dialog width="30%" title="" :visible.sync="innerVisible" append-to-body center="center">
              <el-form :label-position="labelPosition" label-width="100px"  :model="formEdit02" class="demo-form-inline" size="mini">
                <el-form-item label="">
                  <el-input v-model="formEdit02.ItemName" placeholder="" readonly="readonly" ></el-input>
                </el-form-item>
                <el-form-item label="">
                  <el-input v-model="formEdit02.Position" placeholder="" ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="danger" style="width: 100px;" @click="addCheckDetailed"  >
                  </el-button>
                </el-form-item>
              </el-form>
            </el-dialog>
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24" align="right">
            <el-pagination
              background
              :page-size="pageSize" :current-page.sync="currentPage01"
              layout="prev, pager, next"
              :total="totalCount" @current-change="currentChange" v-show="this.categories.length>0">
            </el-pagination>
          </el-col>
        </el-row>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogFormVisible=false" size="mini"></el-button>
        </div>
      </el-dialog>
       <!-- ---end -->

      <el-table stripe ref="multipleTable" size="mini"
        :data="categories02" tooltip-effect="dark" style="width: 100%">
        <el-table-column label="" prop="ItemCode" width="140" align="left" ></el-table-column>
        <el-table-column label="" prop="ItemName" align="left"></el-table-column>
        <el-table-column label="" prop="Format" width="80" align="left"></el-table-column>
        <el-table-column label="" prop="Position" width="80" align="left"></el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini"  type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>

  </el-container>
</template>

<script>
  import {showMsgTitle} from '../../../utils/api'
  import  {showMsgConfirm} from '../../../utils/api'
  import {postRequest}   from '../../../utils/api'
  import {putRequest}    from '../../../utils/api'
  import {deleteRequest} from '../../../utils/api'
  import {getRequest}    from '../../../utils/api'
  export default {
    mounted:function(){
      this.loadDatas('','','');
    },
    methods:{
      handleCurrentChange01:function(val){
        this.formEdit03.ID=val.ID;
        this.formEdit03.Name=val.Name;
        this.formEdit03.CreationTime = val.CreationTime ;
        this.formEdit03.Scope = val.Scope+'';
        this.formEdit03.RecordType = val.RecordType+'';
        this.showTmplChecks(val.ID);
      },
      showTmplChecks(CheckTempID){
        let _this = this;
        getRequest('/neupha/templateDetaile/listTemplateChecks?keywords01='+CheckTempID).then(resp=> {
          if(resp.status == 200){
            this.categories02 = resp.data.list;
          }else{
            _this.$message({type:'warm',message:''});
          }
        });
      },
      //
      showInnerDialog(index,row){
        this.innerVisible=true;
        this.formEdit02 = row;
        this.formEdit02.CheckProjID = row.ID;
        this.formEdit02.CheckTempID = this.formEdit03.ID;
      },
      //
      addCheckDetailed(){
        let _this = this;
        postRequest('/neupha/templateDetaile/check',this.formEdit02).then(resp=> {
          if(resp.status == 200){
            let json = resp.data;
            _this.$message({type:json.status,message:json.msg});
            _this.formEdit02 = {};
            this.innerVisible=false;
            _this.showTmplChecks(_this.formEdit03.ID)
          }else{
            _this.$message({type:'warm',message:''});
          }
        });
      },
      //
      handleDelete(index,row){
        let _this = this;
        this.$confirm(' ,?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteRequest('/neupha/templateDetaile/checks/'+row.ID).then(resp => {
            if (resp.status == 200) {
              let json = resp.data;
              _this.$message({type: json.status, message: json.msg});
              _this.showTmplChecks(_this.formEdit03.ID);
            }
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      updataTemplate(){
        let _this = this;
        this.$confirm(' ,?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          putRequest('/neupha/template/check', this.formEdit03).then(resp => {
            if (resp.status == 200) {
              let json = resp.data;
              _this.$message({type: json.status, message: json.msg});
              _this.loadDatas('', '','');
            }
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //
      deleteTemplate(){
        let _this = this;
        this.$confirm(' ,?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deleteRequest('/neupha/template/check/'+this.formEdit03.ID).then(resp => {
            if (resp.status == 200) {
              let json = resp.data;
              _this.$message({type: json.status, message: json.msg});
              _this.formEdit03 = {CreationTime:new Date()};
              _this.categories02=[];
              _this.loadDatas('','','');
            }
          });
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      searchTemplate(){
        this.loadDatas(this.searchForm01.Name,this.searchForm01.Scope,this.searchForm01.RecordType);
      },
      addNewData(){
        let _this = this;
        postRequest('/neupha/template/check', this.dialogForm01).then(resp => {
          if (resp.status == 200) {
            let json = resp.data;
            _this.$message({type: json.status, message: json.msg});
            this.dialogForm01.Name='';
            _this.loadDatas('', '','');
          }
        });
        _this.dialogTableVisible = false;
      },
      // -- 
      loadDatas(keywords01,keywords02,keywords03){
        let _this = this;
        let url = "/neupha/template/listCheckTemplate?keywords01="+keywords01+"&keywords02="+keywords02+"&keywords03="+keywords03;
        getRequest(url).then(resp=>{
          if(resp.status == 200){
            _this.templateData01 = resp.data ;
          }else{
            _this.templateData01.Name = '';
          }
        }).catch(resp=>{
          _this.$message({type:'error',message:''});
        });
      },

      searchData(){
        this.loadFmeDatas(1,this.pageSize,this.keywords01)
      },
      showFme(){
        this.dialogFormVisible=true;
        this.loadFmeDatas(1,10,'');
      },
      //
      currentChange(currentPage){
        this.currentPage01 = currentPage;
        this.loading = true;
        this.loadFmeDatas(currentPage, this.pageSize,this.keywords01);
      },
      //
      loadFmeDatas(pageNumber, pageSize ,keywords01){
        let _this = this;
        let url = "/neuski/fme/listPage?pageNumber=" + pageNumber + "&pageSize=" + pageSize + "&keywords01=" + keywords01 ;
        getRequest(url).then(resp=> {
          _this.loading = false;
          if (resp.status == 200) {
            _this.categories = resp.data.list;
            _this.totalCount = resp.data.totalCount;
            _this.currentPage01 = pageNumber ;
          } else {
            _this.$message({type: 'error', message: '!'});
          }
        }, resp=> {
          _this.loading = false;
          if (resp.response.status == 403) {
            _this.$message({type: 'error', message: resp.response.data});
          } else {
            _this.$message({type: 'error', message: '!'});
          }
        }).catch(resp=> {
          //
          _this.loading = false;
          _this.$message({type: 'error', message: '!'});
        });
      },
    },
    data() {
      return {
        dialogTableVisible: false,
        dialogFormVisible:false,
        innerVisible:false,
        categories:[],
        categories02:[],
        pageSize:10,
        totalCount:0,
        currentPage01:1,
        keywords01: '',
        formEdit02:{CheckProjID:'',CheckTempID:'',Position:''},
        formEdit03:{ID:'',Name:'',Scope:'',UserID:'1',CreationTime:new Date(),RecordType:''},
        dialogForm01:{ID:'',Name:'',Scope:'1',UserID:'1',CreationTime:'2019-03-08 10:18',RecordType:'1'},
        searchForm01:{Name:'',Scope:'',RecordType:''},
        templateData01: [],


        selItems:[],
        labelPosition:'right',

      }
    },
    filters:{
      showScope:function(scope){
        if(scope==1){
          return '';
        }else if(scope==2){
          return '';
        }else{
          return '';
        }
      },
      showType:function(scope){
        if(scope==1){
          return '';
        }else if(scope==2){
          return '';
        }else{
          return '';
        }
      }
    },
  }
</script>

<style>
</style>



