<template>
	<div class="container" >
    <!-- 2. -->
	<el-container direction="horizontal">

    <!-- ---start -->
    <el-dialog title="" :visible.sync="dialogFormVisible" width="80%">
      <el-row>
        <el-col :span="18">
          <div class="grid-content bg-purple">
            <el-input placeholder="" size="mini" v-model="keywords01"></el-input>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <el-button type="primary" size="mini" style="margin-left: 10px" icon="el-icon-view" @click="searchData"></el-button>
          </div>
        </el-col>
      </el-row>
      <el-table stripe ref="multipleTable" size="mini" :data="categories" tooltip-effect="dark" style="width: 100%">
        <el-table-column label="" prop="ItemCode" width="140" align="left" ></el-table-column>
        <el-table-column label="" prop="ItemName" align="left"></el-table-column>
        <el-table-column label="" prop="Format" width="80" align="left"></el-table-column>
        <el-table-column label="" prop="Price" width="80" align="left"></el-table-column>
        <el-table-column label="" prop="MnemonicCode" width="180" align="left"></el-table-column>
        <el-table-column label="" align="center" width="80">
          <template slot-scope="scope">
            <el-button type="primary"  size="mini" @click="showInnerDialog(scope.$index, scope.row)">
            </el-button>
          </template>
          <el-dialog width="30%" title="" :visible.sync="innerVisible" append-to-body center="center">
            <el-form label-width="100px"  :model="formEdit02" class="demo-form-inline" size="mini">
              <el-form-item label="">
                <el-input v-model="formEdit02.Name" placeholder="" ></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-tag type="info">{{ Temp.TempItemName }}</el-tag>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="formEdit02.Objective" placeholder="" ></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="formEdit02.Position" placeholder="" ></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-switch v-model="formEdit02.IsUrgent" active-text="" inactive-text=""></el-switch>
              </el-form-item>
              <!--
              <el-form-item label="">
                <el-input-number v-model="formEdit02.Num" @change="applyDialog01" :min="1" :max="10"></el-input-number>
              </el-form-item>
              -->
              <el-form-item>
                <el-button type="primary" style="width: 100px;" @click="addCheckDetailed"  >
                </el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </el-table-column>
      </el-table>
      <el-row>
        <el-col :span="24" align="right">
          <el-pagination
            background
            :page-size="pageSize" :current-page.sync="currentPage01"
            layout="prev, pager, next"
            :total="totalCount" @current-change="currentChange" v-show="this.categories.length>0">
          </el-pagination>
        </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogFormVisible=false" size="mini"></el-button>
      </div>
    </el-dialog>
    <!-- ---end -->


    <!--  -->
		<el-aside  style="width: 700px;height: 500px;">
		  	<el-row style="background-color: #EAF1F5">
		  		<el-col :span="6" style="margin-top: 4px;">

		  		</el-col>
		  		<el-col :span="3"><el-button type="text" size="mini" class="el-icon-circle-plus" @click="showFme"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-remove" :disabled="this.selItems.length==0" @click="deleteAll"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-success" :disabled="this.selItems.length==0" @click="upToSaved"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-delete" :disabled="this.selItems.length==0" @click="upToBad"></el-button></el-col>
          <el-col :span="3"><el-button type="text" size="mini" class="el-icon-circle-plus-outline" :disabled="this.selItems.length==0" >*</el-button></el-col>
          <el-col :span="3"><el-button type="text" size="small" class="el-icon-circle-plus-outline" @click="refApply"></el-button></el-col>
        </el-row>
      <el-row>
        <el-col>
          <el-tag size="mini"></el-tag>
          <el-tag type="warning" size="mini">{{sumPrice}}</el-tag>
        </el-col>
      </el-row>
		  	<el-row>
		  		<el-col :span="22" style="margin-top: 4px;">
		  		<el-table ref="singleTable" :data="tableData02"
				    style="width: 100%;margin: 0px;padding: 0px;" :show-header="true" size="mini" @selection-change="handleSelectionChange">
				    <el-table-column  type="selection">
				    </el-table-column>
				    <el-table-column property="Name" label="" width="100">
				    </el-table-column>
				    <el-table-column property="ItemName" label="" >
				    </el-table-column>
				    <el-table-column property="DeptName" label="" width="100">
				    </el-table-column>
            <el-table-column property="State" label="" width="100">
              <template slot-scope="scope">
                {{ scope.row.State|showState }}
              </template>
            </el-table-column>
				    <el-table-column property="Price" label="" width="80">
				    </el-table-column>
				    <el-table-column property="Result" label="" width="80" >
				      <template slot-scope="scope">
                <el-popover   placement="right"
                  width="400" :content="scope.row.Result" trigger="click">
                  <el-button slot="reference" type="text" size="mini"></el-button>
                </el-popover>
				      </template>
				    </el-table-column>
				</el-table>
				</el-col>
		  	</el-row>
		</el-aside>
  		<el-main style="margin-top: -20px;">
  			<el-tabs type="card" >
		    <el-tab-pane label="" >
			    <el-table :data="tableData04" style="width: 100%" ref="singleTable" size="mini">
			      <el-table-column prop="Name" label="">
			      </el-table-column>
			      <el-table-column fixed="right" label="" width="120">
				      <template slot-scope="scope">
				      	<el-button @click.native.prevent="addTmplChecks(scope.row)" type="text" size="small">
				          	
				        </el-button>
				        <el-button @click.native.prevent="showTmplChecks(scope.row)" type="text" size="small">
				          	
				        </el-button>
				      </template>
			      </el-table-column>
			    </el-table> 
		    </el-tab-pane>
			</el-tabs>
        <el-dialog title="" :visible.sync="applyDialogShow" width="40%">
          <el-table stripe ref="multipleTable" size="mini"
                    :data="categories02" tooltip-effect="dark" style="width: 100%">
            <el-table-column label="" prop="ItemCode" width="140" align="left" ></el-table-column>
            <el-table-column label="" prop="ItemName" align="left"></el-table-column>
            <el-table-column label="" prop="Format" width="80" align="left"></el-table-column>
            <el-table-column label="" prop="Position" width="80" align="left"></el-table-column>
          </el-table>
          <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="applyDialogShow=false" size="mini"></el-button>
          </div>
        </el-dialog>
  		</el-main>
	</el-container>
	
   </div>
</template>

<script>
import {showMsgTitle} from '../../../utils/api'
import  {showMsgConfirm} from '../../../utils/api'
import {postRequest}   from '../../../utils/api'
import {putRequest}    from '../../../utils/api'
import {deleteRequest} from '../../../utils/api'
import {getRequest}    from '../../../utils/api'
export default {
  mounted:function(){
    this.loadApply(this.$store.state.Register.ID,'',1);
    this.loadTemplateByDoc(this.$store.state.Register.UserID,1,'');
    this.loadSessionPatient();
  },
  methods: {
    refApply(){
      this.loadApply(this.$store.state.Register.ID,'',1);
      this.loadTemplateByDoc(this.$store.state.Register.UserID,1,'');
      this.loadSessionPatient();
    },
    showConfirm(msg) {
      showMsgConfirm(this, msg);
    },
    showMsg(msg) {
      showMsgTitle(this, msg);
    },

    //
    showFme(){
      if(this.loadSessionPatient()){
        this.dialogFormVisible=true;
        this.loadFmeDatas(1,10,'',1);
      }else{
        this.$message({type:'warning',message:''});
        return false;
      }
    },
    searchData(){
//todo
    },
    //
    currentChange(currentPage){
      this.currentPage01 = currentPage;
      this.loading = true;
      this.loadFmeDatas(currentPage, this.pageSize,this.keywords01,1);
    },
    //
    loadFmeDatas(pageNumber, pageSize ,MnemonicCode,RecordType){
      let _this = this;
      let url = "/neuski/fme/listPageByRecordtype?"
        +"pageNumber=" + pageNumber + "&pageSize=" + pageSize + "&MnemonicCode=" + MnemonicCode+ "&RecordType=" + RecordType ;
      getRequest(url).then(resp=> {
        _this.loading = false;
        if (resp.status == 200) {
          _this.categories = resp.data.list;
          _this.totalCount = resp.data.totalCount;
          _this.currentPage01 = pageNumber ;
        } else {
          _this.$message({type: 'error', message: '!'});
        }
      });
    },

    //
    showInnerDialog(index,row){
      this.innerVisible=true;
      this.templateToApply(row);     
    },
    templateToApply(row){
      this.formEdit02 = row;
      this.formEdit02.CreationTime = new Date();
      this.Temp.TempItemName = row.ItemName;

      this.formEdit02.MedicalID = this.sessionPatient.ID;
      this.formEdit02.RegistID = this.sessionPatient.RegistID;
      this.formEdit02.ItemID = row.ID;
      this.formEdit02.DoctorID = 1; //ID1 TODO
      this.formEdit02.CheckOperID = 2; //ID2 TODO
      this.formEdit02.ResultOperID = 2; //ID2 TODO
    },
    //
    applyDialog01(val){
      this.formEdit02.Num = val;
    },
    //
    addCheckDetailed(){
      let _this = this;
      this.formEdit02.State = '1';
      this.formEdit02.RecordType = '1';
      postRequest('/neudoc/apply/check', this.formEdit02).then(resp => {
        if (resp.status == 200) {
          let json = resp.data;
          _this.$message({type: json.status, message: json.msg});
          this.loadApply(this.$store.state.Register.ID,'',1);
        }
      });
      // console.log(this.formEdit02);
      this.innerVisible = false;
    },
    //
    handleSelectionChange(val){
      this.selItems = val;
      this.sumPrice = 0;
      for(let i=0;i<val.length;i++){
        this.sumPrice += val[i].Price;
      }
    },
    //
    deleteAll(){
      let tableData02 = this.tableData02;
      let selItems = this.selItems;

      for(let j=0;j<selItems.length;j++){
        if(selItems[j].State != 1){
          this.$message({type:'warning',message:''});
          return false;
        }
      }

      this.$confirm(' ' +  this.selItems.length + ' ?', '', {
        type: 'warning',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(()=> {
        // let selItems = this.selItems;
        let ids = '';
        for (let i = 0; i < selItems.length; i++) {
          ids += selItems[i].ID + ",";
        }
        this.deleteData(ids);

      }).catch(() => {
        this.$message({type: 'info',message: ''});
      });
    },
    deleteData(ids){
      let _this = this;
      this.loading = true;
      //
      deleteRequest('/neudoc/apply/check/' + ids).then(resp=> {
        if (resp.status == 200) {
          let json = resp.data;
          _this.$message({type: json.status,message: json.msg});
          _this.loadApply(this.$store.state.Register.ID,'',1);
        }else{
          _this.$message({type: 'error', message: '!'});
        }
      });
    },
    //
    upToSaved(){
      let selItems = this.selItems;
      this.$confirm(' ' +  this.selItems.length + ' ?', '', {
        type: 'warning',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(()=> {
        this.updateData(selItems,2);
      }).catch(() => {
        this.$message({type: 'info',message: ''});
      });
    },
    //
    upToBad(){
      let selItems = this.selItems;
      for(let j=0;j<selItems.length;j++){
        if(selItems[j].State != 2){
          this.$message({type:'warning',message:''});
          return false;
        }
      }
      this.$confirm(' ' +  this.selItems.length + ' ?', '', {
        type: 'warning',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(()=> {
        this.updateData(selItems,0);
      }).catch(() => {
        this.$message({type: 'info',message: ''});
      });
    },
    //
    updateData(selItems,state){
      let _this = this;
      let ids = '';
      for (let i = 0; i < selItems.length; i++) {
        ids += selItems[i].ID + ",";
      }
      getRequest('/neudoc/apply/check?ids='+ids+"&state="+state).then(resp=> {
        if (resp.status == 200) {
          let json = resp.data;
          _this.$message({type: json.status,message: json.msg});
          _this.loadApply(this.$store.state.Register.ID,'',1);
        }else{
          _this.$message({type: 'error', message: '!'});
        }
      });
    },
    //
    loadApply(RegistID,State,RecordType){
      let _this = this;
      let url = "/neudoc/apply/listCheck?" +"RegistID=" + RegistID + "&State=" + State + "&RecordType=" + RecordType ;
      getRequest(url).then(resp=> {
        _this.loading = false;
        if (resp.status == 200) {
          _this.tableData02 = resp.data.list;
        } else {
          _this.$message({type: 'error', message: '!'});
        }
      });;
    },
    //
    loadTemplateByDoc(UserID, RecordType, Name){
      let _this = this;
      let url = "/neupha/template/check/listTemplateByDoc?" +"UserID=" + UserID + "&RecordType=" + RecordType + "&Name=" + Name ;
      getRequest(url).then(resp=> {
        _this.loading = false;
        if (resp.status == 200) {
          _this.tableData04 = resp.data.list;
        } else {
          _this.$message({type: 'error', message: '!'});
        }
      });
    },
    //
    loadSessionPatient() {
      let patstr = sessionStorage.getItem('medicalRecord');
      if (patstr != null && patstr.length > 0 && patstr != '{}') {
        this.sessionPatient = JSON.parse(patstr);
        return true;
      } else {
        this.sessionPatient={ID:'',CaseNumber:'',RegistID:'',Readme:'',Present:'',PresentTreat:'',History:'',Allergy:'',
          Physique:'',Proposal:'',Careful:'',CheckResult:'',Diagnosis:'',Handling:'',CaseState:''};
        this.$message({type: 'info', message: ''});
        return false;
      }
    },

    //
    addTmplChecks(CheckTemp){
      let _this = this;
      this.$confirm(',?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        getRequest('/neupha/templateDetaile/listTemplateChecks?keywords01='+CheckTemp.ID).then(resp=> {
          if(resp.status == 200){
            this.categories02 = resp.data.list;
            if(this.categories02!=null){
              for(let i=0;i<this.categories02.length;i++){
                this.templateToApply(this.categories02[i]);
                this.formEdit02.Name = ''+CheckTemp.Name;
                this.formEdit02.ItemID = this.categories02[i].CheckProjID;
                this.formEdit02.Num = 1;
                this.formEdit02.State = '1';
                this.formEdit02.RecordType = '1';
                postRequest('/neudoc/apply/check', this.formEdit02).then(resp => {
                  if (resp.status == 200) {
                    let json = resp.data;
                    _this.$message({type: json.status, message: json.msg});
                    this.loadApply(this.$store.state.Register.ID,'',1);
                  }else{
                    this.$message({
                      type: 'warning',
                      message: '!'
                    });
                  }
                });
              }
            }
          }else{
            _this.$message({type:'warm',message:''});
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    showTmplChecks(CheckTemp){
      console.log(CheckTemp.ID);
      this.applyDialogShow = true;
      this.listTmplChecks(CheckTemp.ID)
    },
    //
    listTmplChecks(CheckTempID){
      let _this = this;
      getRequest('/neupha/templateDetaile/listTemplateChecks?keywords01='+CheckTempID).then(resp=> {
        if(resp.status == 200){
          this.categories02 = resp.data.list;
        }else{
          _this.$message({type:'warm',message:''});
        }
      });
    },
  },
  data() {
  	return {
      loading : false,
      dialogTableVisible: false,
      dialogFormVisible:false,
      innerVisible:false,
      applyDialogShow:false,
      categories:[],
      categories02:[],
      pageSize:10,
      totalCount:0,
      currentPage01:1,
      keywords01: '',
      formEdit02:{ID:'',MedicalID:'',RegistID:'',ItemID:'',Name:'',Objective:'',Position:'',IsUrgent:false,Num:1,CreationTime:new Date(),
        DoctorID:'',CheckOperID:'',ResultOperID:'',CheckTime:new Date(),Result:'',ResultTime:new Date(),State:'1',RecordType:'1'},
      tableData02:[],
      tableData04:[],
      selItems:[],
      // -- sessionStorage
      sessionPatient:{ID:'',CaseNumber:'',RegistID:'',Readme:'',Present:'',PresentTreat:'',History:'',Allergy:'',
        Physique:'',Proposal:'',Careful:'',CheckResult:'',Diagnosis:'',Handling:'',CaseState:''},
      Temp:{TempItemName:''},
      sumPrice:0,

      formEdit03:{ID:'',Name:'',Scope:'',UserID:'1',CreationTime:new Date(),RecordType:''},
      dialogForm01:{ID:'',Name:'',Scope:'1',UserID:'1',CreationTime:'2019-03-08 10:18',RecordType:'1'},
      searchForm01:{Name:'',Scope:'',RecordType:''},
      templateData01: [],
  	}
  },
  filters:{
    showState:function(state){
      if(state=='1'){
        return '';
      }else if(state=='2'){
        return '';
      }else if(state=='3'){
        return '';
      }else if(state=='4'){
        return '';
      }else if(state=='5'){
        return '';
      }else if(state=='6'){
        return '';
      }else{
        return '';
      }
    },

  },
}
</script>

<style>
</style>



