                <el-select  placeholder="" v-model="eltab02">
                  <el-option label="" value="neike"></el-option>
                  <el-option label="" value="menzen"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input ></el-input>
              </el-form-item>
