  export default {
    methods:{
      nodeClick:function (clickDate) {
        this.siteName = clickDate.label;
      },
      onSubmit:function(){
        this.dialogTableVisible=false;
      }
    },
    data() {
      return {
        siteName:"",
        radio01:1,
        dialogTableVisible: false,
        eltab01:'first',
        eltab02:'',

        treeData: [{
