          mingcheng: '5%',
          guige:'250ml*1',
          danjia:'2/',
          yongliang:'250',
          danwei:'ml',
          pinci:'',
          tianshu:'3',
          cishu:'3',
          shuliang:'3',
          danwei:'',
          zhutuo:'',
          jieguo:''
        }, {
          mingcheng: '5%',
          guige:'250ml*1',
          danjia:'2/',
          yongliang:'250',
          danwei:'ml',
          pinci:'',
          tianshu:'3',
          cishu:'3',
          shuliang:'3',
          danwei:'',
          zhutuo:'',
          jieguo:''
        }],
      }
    }
  }
</script>

<style>
</style>



