import Vue from 'vue'
import Router from 'vue-router'

//
import Login from '@/components/Login'
//
import Home from '@/components/Home'

//
import Register from '@/components/neureg/Register' //
import Charge   from '@/components/neureg/Charge'   //
import Refund   from '@/components/neureg/Refund'   //
import Signout  from '@/components/neureg/Signout'  //
import Makeup   from '@/components/neureg/Makeup'   //
import Replay   from '@/components/neureg/Replay'   //
import Daily    from '@/components/neureg/Daily'     //
import Daily2   from '@/components/neureg/Daily2'    //
import Patient from '@/components/neureg/Patient'    //

//
import Count01  from '@/components/neufin/Count01'  //
import Count02  from '@/components/neufin/Count02'  //
import Count03  from '@/components/neufin/Count03'  //
import Cost     from '@/components/neufin/Cost'     //

//
import docHome from '@/components/neudoc/home'              //
import suite01 from '@/components/neudoc/his2/suite01.vue'  //
import suite02 from '@/components/neudoc/his2/suite02.vue'  //
import suite03 from '@/components/neudoc/his2/suite03.vue'  //
import suite04 from '@/components/neudoc/his2/suite04.vue'  //

//
import tech01 from '@/components/neuski/tech01.vue'  //
import tech02 from '@/components/neuski/tech02.vue'  //
import tech03 from '@/components/neuski/tech03.vue'  //
import tech04 from '@/components/neuski/tech04.vue'  //

//
import shop01 from '@/components/neupha/shop01.vue'  //
import shop02 from '@/components/neupha/shop02.vue'  //
import shop03 from '@/components/neupha/shop03.vue'  //

//
import Constant from '@/components/neusys/Constant'             //
import Dept from '@/components/neusys/Dept'                     //
import User from '@/components/neusys/User'                     //
import RegistLevel from '@/components/neusys/RegistLevel'       //
import SettleCategory from '@/components/neusys/SettleCategory' //
import DiseCategory from '@/components/neusys/DiseCategory'     //
import Project from '@/components/neusys/Project'               //

import Scheduling from '@/components/neusys/Scheduling_new' // new

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: '',
      hidden: true,
      component: Login,  //Login   Home
      meta: {
        isLogin: false
      }
    },
    {
      path: '/home',
      name: '',
      component: Home,
      hidden: true,
      meta: {
        isLogin: true
      }
    }, {
      path: '/home',
      component: Home,
      name: '',
      iconCls: 'fa fa-file-text-o',
      children: [
        {
          path: '/Register',
          name: '',
          component: Register,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }, {
          path: '/Signout',
          name: '',
          component: Signout,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Charge',
          name: '',
          component: Charge,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }, {
          path: '/Refund',
          name: '',
          component: Refund,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Makeup',
          name: '',
          component: Makeup,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Replay',
          name: '',
          component: Replay,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Patient',
          name: '',
          component: Patient,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Daily',
          name: '*',
          component: Daily,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Daily2',
          name: '*',
          component: Daily2,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }
      ]
    },{
      path: '/home',
      component: Home,
      name: '',
      children: [
        {
          path: '/docHome',
          iconCls: 'fa fa-user-o',
          name: '',
          component: docHome,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/suite02',
          name: '',
          component: suite02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/suite03',
          name: '',
          component: suite03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/suite04',
          name: '*',
          component: suite04,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },
      ]
    },{
      path: '/home',
      component: Home,
      name: '*',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/tech01',
          name: '*',
          component: tech01,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/tech02',
          name: '*',
          component: tech02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/tech03',
          name: '*',
          component: tech03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/tech04',
          name: '*',
          component: tech04,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }]},{
      path: '/home',
      component: Home,
      name: '',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/shop01',
          name: '',
          component: shop01,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/shop02',
          name: '',
          component: shop02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/shop03',
          name: '',
          component: shop03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }]
    },{
      path: '/home',
      component: Home,
      name: '',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/Count01',
          name: '*',
          component: Count01,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Count02',
          name: '*',
          component: Count02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Count03',
          name: '*',
          component: Count03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Cost',
          name: '',
          component: Cost,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }
      ]
    } ,{
      path: '/home',
      component: Home,
      name: '',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/Constant',
          name: '',
          component: Constant,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Dept',
          name: '',
          component: Dept,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/User',
          name: '',
          component: User,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/RegistLevel',
          name: '',
          component: RegistLevel,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/SettleCategory',
          name: '',
          component: SettleCategory,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/DiseCategory',
          name: '',
          component: DiseCategory,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Project',
          name: '',
          component: Project,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Scheduling',
          name: '',
          component: Scheduling,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }
      ]
    }
  ]
})



