<el-row :gutter="2">
<el-col :span="4">
  <div class="grid-content bg-purple">
    <font color="red">*</font>
    <font style="font-size:13px;font-family:'Microsoft YaHei'">:</font>&nbsp;   
         <el-select  placeholder="" style="width: 100px" size="mini" v-model="SettleID">
              <el-option
                    v-for="item in categories4"
                    :key="item.ID"
                    :label="item.SettleName"
                    :value="item.ID">
              </el-option>
         </el-select>
  </div>
  </el-col>
  <el-col :span="5">
     <div class="grid-content bg-purple">
     <font color="red">*</font>
     <font style="font-size:13px;font-family:'Microsoft YaHei'">:
     <el-date-picker
                  v-model="VisitDate"
                  type="date"
                  placeholder=""  size="mini" style="width: 125px" value-format="yyyy-MM-dd"  @change="kzrq_select()">
     </el-date-picker></font>
     </div>
  </el-col>
  <el-col :span="4">
  <div class="grid-content bg-purple">
    <font color="red">*</font>
    <font style="font-size:13px;font-family:'Microsoft YaHei'">:</font>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp   
         <el-select  placeholder="" style="width: 90px" size="mini" v-model="Noon" @change="Noon_select()">
          <el-option
                    v-for="item in categories5"
